const wrapper = document.querySelector('.wrapper');
const pagination = document.querySelector('.js-pagination');
// const listPag = document.querySelector('.list-pagination');

// /* ---------------------------------  -------------------------------- */

let users = [
  //     

  { name: 'name1', age: 23 },
  { name: 'name2', age: 23 },
  { name: 'name3', age: 23 },
  { name: 'name4', age: 23 },
  { name: 'name5', age: 23 },
  { name: 'name6', age: 23 },
  { name: 'name7', age: 23 },
  { name: 'name8', age: 23 },
  { name: 'name9', age: 23 },
  { name: 'name10', age: 23 },
  { name: 'name11', age: 23 },
];
let numOfCardsPerPage = 2;
let countOfItems = Math.ceil(users.length / numOfCardsPerPage); //    
let items = [];
/* -----------------------  ---------------------- */

for (let i = 1; i <= countOfItems; i++) {
  let li = document.createElement('li');
  li.classList = 'num';

  li.innerHTML = i;

  // console.log(li);

  pagination.appendChild(li);
  items.push(li);
}

/* ---------------------     --------------------- */
showPage(items[0]);

//           

for (const item of items) {
  item.addEventListener('click', function () {
    showPage(item);
    // console.log(item);
  });
}
/* --------------------     ------------------- */
function showPage(item) {
  let pageNum = +item.innerHTML;
  let start = (pageNum - 1) * numOfCardsPerPage;
  let end = start + numOfCardsPerPage;
  let notes = users.slice(start, end); //  API  users

  removeCurrentColorPage();

  currentColorPage(item);

  clearPage();

  createUl();

  addsLiToUl(notes);
}

/* --------------------     ------------------- */
function currentColorPage(item) {
  item.classList.add('active');
}
/* ----------------------------         ---------------------------- */
function removeCurrentColorPage() {
  let activeLi = document.querySelector('.active');
  if (activeLi) {
    activeLi.classList.remove('active');
  }
}

/* ----------------------------       ---------------------------- */
function clearPage() {
  wrapper.innerHTML = '';
}

/* ----------------------------     ul--------------------------- */
function createUl() {
  let ul = document.createElement('ul');
  wrapper.appendChild(ul);
  ul.classList = 'movie-list';
}

/* ----------------------------   li   ul--------------------------- */
function addsLiToUl(notes) {
  let listPag = document.querySelector('.movie-list');
  for (const note of notes) {
    let itemMovie = [];

    let li = document.createElement('li');
    li.innerHTML = note.name; //     
    listPag.appendChild(li);
    itemMovie.push(li);
  }
}

